//日期分割
import {
	friendlyDate
} from '@/common/util.js';

var _self, page=1,timer=null;
export default {
	data() {
		return {
			regulations:[],
			lodingText:'加载更多…'
		}
	},
	
	onLoad:function(){
		_self=this;
		this.getNews();
	},
	//下拉刷新
	onPullDownRefresh:function(){
		this.getNews();
	},
	//上拉加载
	onReachBottom:function(){
		// 延迟加载
		if(timer!=null){clearTimeout(timer);}//多次上拉清除延迟，保证只延迟一次
		timer=setTimeout(function(){_self.getMoreNews();},500);//延迟0.5秒加载
	},
	
	methods: {
		getNews:function(){
			page=1;
			_self.lodingText='加载更多…';
			// 加载提示
			uni.showNavigationBarLoading();
			uni.request({
				// 请求头
				header: {
					'sessionId': 'fa2e5c5698b0480eb68ec05fa9a35a3d' //自定义请求头信息
				},
				url:'http://192.168.1.22:30917/spMgRegulate/query?length=10&pageNum='+page,
					success:function(res){
						const data=res.data.data[0].results;
						// console.log(data);
						const data_list = data.map((news) => {
							return{
								inputman :news.inputman,
								inputdate:friendlyDate(new Date(news.inputdate).getTime()),
								regulateid:news.regulateid
							};	
						});
						
						 _self.regulations=data_list;
						uni.stopPullDownRefresh();
						uni.hideNavigationBarLoading();
						page++;
					},
					
				})
			},
			
			
			
			getMoreNews:function(){
				// 如果已经判断没有数据，则不执行该方法
				if(_self.lodingText=='加载完毕!'){return false}
				_self.lodingText='加载更多…';
				uni.showNavigationBarLoading();
				uni.request({
					header: {
						'sessionId': 'fa2e5c5698b0480eb68ec05fa9a35a3d' //自定义请求头信息
					},
					url:'http://192.168.1.22:30917/spMgRegulate/query?length=10&pageNum='+page,
					success:function(res){
						console.log(res.data.data[0].results)
						// 判断如果下拉加载为空则停止该方法
						if(res.data.data[0].results[0]==null){
							_self.lodingText='加载完毕!'
							uni.stopPullDownRefresh();
							uni.hideNavigationBarLoading();
							return false;
						}
						const data=res.data.data[0].results;
						const data_list = data.map((news) => {
							return{
								inputman :news.inputman,
								inputdate:friendlyDate(new Date(news.inputdate).getTime()),
								regulateid:news.regulateid
							};
							
						});
						// 累加不覆盖进行下拉加
						_self.regulations=_self.regulations.concat(data_list);
						uni.stopPullDownRefresh();
						uni.hideNavigationBarLoading();
						page++;
					}
				})
			}
					
	}
	
}
